.autocomplete-suggestions {
    border: 1px solid #999999;
    background: #FFFFFF;
    overflow: auto;

    .autocomplete-suggestion {
        cursor: pointer;
        padding: 2px 5px;
        white-space: nowrap;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
    }

    .autocomplete-selected {
        background: #f0f0f0;
    }

    strong {
        font-weight: normal;
        color: #00A65A;
    }

    .autocomplete-group {
        padding: 2px 5px;

        strong {
            display: block;
            border-bottom: 1px solid #000000;
        }
    }
}

button {
    outline: 0;
}

.records-list {
    .row-actions {
        position: static;
        color: #dddddd;
        text-transform: capitalize;

        * {
            word-wrap: break-word;
        }

        span {
            height: 24px;
        }

        .edit {
            font-weight: bold;
        }
    }

    tr:hover .row-actions {
        position: static;
    }
}


input[type=checkbox], input[type=radio] {
    position: relative !important;
    border: 1px solid #b4b9be;
    background: #fff;
    color: #555;
    clear: none;
    cursor: pointer;
    display: inline-block;
    line-height: 0;
    height: 16px;
    margin: -4px 4px 0 0;
    outline: 0 !important;
    padding: 0 !important;
    text-align: center;
    vertical-align: middle;
    width: 16px;
    min-width: 16px;
    -webkit-appearance: none;
    -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, .1);
    -webkit-transition: .05s border-color ease-in-out;
    transition: .05s border-color ease-in-out
}
input[type=checkbox]:checked:before,
input[type=radio]:checked:before {
    float: left;
    display: inline-block;
    vertical-align: middle;
    width: 14px;
    outline: 0 !important;
    font: 400 21px/1 dashicons;
    -webkit-font-smoothing: antialiased;
}
input[type=checkbox]:checked:before {
    content: "\2714";
    margin: -2px 0 0 -1px;
    color: #1e8cbe
}
input[type=radio]:checked:before {
    content: "\2022";
    text-indent: -9999px;
    -webkit-border-radius: 50px;
    border-radius: 50px;
    font-size: 24px;
    width: 6px;
    height: 6px;
    margin: 4px;
    line-height: 16px;
    background-color: #1e8cbe
}

.pagination {
    margin: 0;
}

@media only screen and (min-width: 768px) {
    #media-modal .modal-dialog {
        width: 95%
    }
}

#media-modal .modal-body {
    max-height: calc(100vh - 190px);
    overflow-y: auto;
}

.library-grid {
    overflow: auto;
    margin: 0;
}
.grid {
    float: left;
    position: relative;
    width: 20%;
    padding-bottom: 20%;
}
.gridInner {
    position: absolute;
    left: 10px;
    right: 10px;
    top: 10px;
    bottom: 10px;
    overflow: hidden;
    background-color: #dddddd;
}
.gridInner .select-box {
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    display: none;
    background: url('../image/sprite.png') -40px 0 no-repeat;
}
.gridInner.selected .select-box {
    text-transform: none;
    display: block;
}
.gridInner img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.gridInner .titleGrid {
    //display: inline-block;
    //white-space: nowrap;
    //overflow: hidden;
    //-ms-text-overflow: ellipsis;
    //text-overflow: ellipsis;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    opacity: 0;
    //margin-bottom: -50px;
    background: #000;
    background: rgba(0, 0, 0, 0.5);
    color: #FFF;
    padding: 5px;
    text-align: center;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}
.titleGrid:hover {
    opacity: 1;
    //margin-bottom: 0;
}
.gridInner .dz-remove {
    cursor: pointer;
    height: auto;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    background: rgba(255, 0, 0, 0.5);
    color: #FFF;
    padding: 5px;
    text-align: center;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.dz-remove:hover {
    opacity: 1;
}

@media only screen and (max-width : 300px) {
    /* Smartphone view: 1 tile */
    .grid {
        width: 100%;
        padding-bottom: 100%;
    }
}
@media only screen and (max-width : 320px) and (min-width : 301px) {
    /* Tablet view: 2 tiles */
    .grid {
        width: 50%;
        padding-bottom: 50%;
    }
}
@media only screen and (max-width : 480px) and (min-width : 321px) {
    /* Small desktop / ipad view: 3 tiles */
    .grid {
        width: 33.3333333%;
        padding-bottom: 33.3%;
    }
}
@media only screen and (max-width : 640px) and (min-width : 481px) {
    /* Medium desktop: 4 tiles */
    .grid {
        width: 25%;
        padding-bottom: 25%;
    }
}
@media only screen and (max-width : 960px) and (min-width : 641px) {
    /* Medium desktop: 5 tiles */
    .grid {
        width: 20%;
        padding-bottom: 20%;
    }
}
@media only screen and (max-width : 1024px) and (min-width : 961px) {
    /* Medium desktop: 6 tiles */
    .grid {
        width: 16.6667%;
        padding-bottom: 16.6667%;
    }
}
@media only screen and (max-width : 1280px) and (min-width : 1025px) {
    /* Medium desktop: 7 tiles */
    .grid {
        width: 14.2857%;
        padding-bottom: 14.2857%;
    }
}
@media only screen and (max-width : 1366px) and (min-width : 1281px) {
    /* Medium desktop: 8 tiles */
    .grid {
        width: 12.5%;
        padding-bottom: 12.5%;
    }
}

@media only screen and (min-width: 1367px) {
    /* Medium desktop: 10 tiles */
    .grid {
        width: 10%;
        padding-bottom: 10%;
    }
}